<template>
  <div class="users">
    <breaks first="用户管理" scenter="用户列表"></breaks>
    <el-card class="box-card">
      <div class="inpnt-type">
        <el-input placeholder="请输入内容" class="input-with-select" v-model="params.query">
          <el-button slot="append" icon="el-icon-search" @click="getUsers"></el-button>
        </el-input>
        <!-- 1-1点击添加按钮弹出对话框 -->
        <el-button type="primary" @click="dialogFormVisible = true">添加用户</el-button>
        <!-- 添加的对话框 -->
        <el-dialog title="添加用户" :visible.sync="dialogFormVisible" @close="close">
          <el-form :model="form" :rules="rules" ref="ruleAdd">
            <el-form-item label="用户名" :label-width="formLabelWidth" prop="username">
              <el-input v-model="form.username" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" :label-width="formLabelWidth" prop="password">
              <el-input type="password" v-model="form.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
              <el-input v-model="form.email" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="电话" :label-width="formLabelWidth" prop="mobile">
              <el-input v-model="form.mobile" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="userpost">确 定</el-button>
          </div>
        </el-dialog>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          label="#"
          type="index"
          width="180">
        </el-table-column>
        <el-table-column label="姓名" width="180" prop="username"></el-table-column>
        <el-table-column label="邮箱" width="180" prop="email"></el-table-column>
        <el-table-column label="电话" width="180" prop="mobile"></el-table-column>
        <el-table-column label="角色" width="180" prop="role_name"></el-table-column>
        <el-table-column
          label="状态"
          width="180">
          <template slot-scope="scoped">
            <el-switch
              v-model="scoped.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ccc" @change="checkgloba(scoped.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scoped">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="updateById(scoped.row)"></el-button>
            <!-- 删除按钮 -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteByid(scoped.row.id)"></el-button>
            <!-- 分配权限按钮 -->
            <el-button type="warning" icon="el-icon-setting" size="mini"
                       @click="AssignRoles(scoped.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totals">
      </el-pagination>
      <!-- 修改的对话框 -->
      <el-dialog title="编辑用户" :visible.sync="updateFormVisible" @close="close">
        <el-form :model="updateFrom" :rules="rules" ref="ruleUpdate">
          <el-form-item label="用户名" :label-width="formLabelWidth" prop="username">
            <el-input :disabled="true" v-model="updateFrom.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
            <el-input v-model="updateFrom.email" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话" :label-width="formLabelWidth" prop="mobile">
            <el-input v-model="updateFrom.mobile" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="enter">确 定</el-button>
        </div>
      </el-dialog>
      <!--      分配角色对话框-->
      <el-dialog title="分配角色" ref="form" :visible.sync="uaterUser" :model="formRout" @close="close">
        <el-form :model="form" label-width="100px" ref="ruleSelect">
          <el-form-item label="当前的用户：">
            {{ formRout.username }}
          </el-form-item>
          <el-form-item label="当前的角色：">
            {{ formRout.role_name }}
          </el-form-item>
          <el-form-item label="分配新角色：" @close="close">
            <el-select v-model="rid" placeholder="请选择">
              <el-option v-for="item in formRout" :key="item.id" :label="item.roleDesc"
                         v-model="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="uaterUser = false">取 消</el-button>
          <el-button type="primary" @click="enterRole">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    const chackEmail = (rule, value, callback) => {
      const reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/
      if (!reg.test(value)) {
        return callback(new Error('邮箱格式不正确'))
      } else {
        callback()
      }
    }
    const chackMobile = (rule, value, callback) => {
      const reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/
      if (!reg.test(value)) {
        return callback(new Error('手机号不正确'))
      } else {
        callback()
      }
    }
    return {
      tableData: [],
      formRout: {},
      totals: 0,
      params: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      dialogFormVisible: false,
      updateFormVisible: false,
      uaterUser: false,
      form: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      updateFrom: {
        username: '',
        email: '',
        mobile: ''
      },
      rid: '',
      // 1-2校验表单数据
      rules: {
        username: [
          {
            required: true,
            message: '请输入登录名称',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 5,
            message: '长度在 3 到 5 个字符',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入登录密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 12,
            message: '长度在 6 到 12 个字符',
            trigger: 'blur'
          }
        ],
        email: [
          {
            required: true,
            message: '请输入邮箱',
            trigger: 'blur'
          },
          {
            validator: chackEmail,
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '请输入邮箱',
            trigger: 'blur'
          },
          {
            validator: chackMobile,
            trigger: 'blur'
          }
        ]
      },
      formLabelWidth: '80px'
    }
  },
  created () {
    this.getUsers()
  },
  methods: {
    async getUsers () {
      const res = await this.$http('/users', {
        params: this.params
      })
      this.tableData = res.data.data.users
      this.totals = res.data.data.total
    },
    handleSizeChange (val) {
      this.params.pagesize = val
      this.getUsers()
    },
    handleCurrentChange (val) {
      this.params.pagenum = val
      this.getUsers()
    },
    // 添加用户功能
    // 1-3点击确认按钮进行第二次校验
    userpost () {
      this.$refs.ruleAdd.validate(async valid => {
        if (valid) {
          const res = await this.$http.post('/users', this.form)
          console.log(res)
          if (res.data.meta.status !== 201) {
            this.$message.error(res.data.meta.msg)
          } else {
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.dialogFormVisible = false
            //  1-4 校验通过后发送请求，添加成功后关闭对话框清空表单值
            this.$refs.ruleAdd.resetFields()
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 修改用户状态
    async checkgloba (obj) {
      const res = await this.$http.put(`users/${obj.id}/state/${obj.mg_state}`)
      // console.log(res)
      if (res.data.meta.status !== 200) {
        this.$message.error(res.data.meta.msg)
      } else {
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
        this.getUsers()
      }
    },
    // 修改用户信息
    async updateById (obj) {
      console.log(obj)
      const res = await this.$http('/users/' + obj.id)
      console.log(res)
      this.updateFormVisible = true
      this.updateFrom = res.data.data
    },
    // 修改对话框的提交事件
    async enter () {
      this.$refs.ruleUpdate.validate(async valid => {
        if (valid) {
          const res = await this.$http.put('/users/' + this.updateFrom.id, this.updateFrom)
          if (res.data.meta.status !== 200) {
            this.$message.error(res.data.meta.msg)
          } else {
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.updateFormVisible = false
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 删除
    deleteByid (id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const res = await this.$http.delete('/users/' + id)
        console.log(res)
        if (res.data.meta.status !== 200) {
          this.$message.error(res.data.meta.msg)
        } else {
          this.$message({
            message: res.data.meta.msg,
            type: 'success'
          })
          this.getUsers()
        }
      }).catch((err) => {
        return err
      })
    },
    // 分配角色
    async AssignRoles (obj) {
      const datas = await this.$http('/roles')
      const res = datas.data
      this.formRout = { ...obj, ...res.data }
      this.uaterUser = true
    },
    // 提交分配后的用户角色
    async enterRole () {
      console.log(this.formRout.id)
      const datas = await this.$http.put(`users/${this.formRout.id}/role`, {
        rid: this.rid
      })
      const res = datas.data
      if (res.meta.status === 200) {
        this.$message({
          message: res.meta.msg,
          type: 'success'
        })
        this.uaterUser = false
      } else {
        this.$message.error(res.meta.msg)
      }
    },
    // 关闭后的回调
    close () {
      // 重新加载数据
      this.getUsers()
      this.$refs.ruleSelect.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.users {
  width: 100%;
  height: 100%;

  .el-breadcrumb {
    font-size: 12px;
  }

  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 100%;
    margin-top: 15px;
  }

  .inpnt-type {
    .input-with-select {
      width: 30%;
      margin-right: 20px;
    }
  }

  .el-table {
    margin: 15px 0;
  }
}
</style>
